.chat{
    flex: 1.5 1;    
    display: flex;
    flex-direction: column;
    margin-left: 20em;
}

.chat-room-name{
    margin: 0px;
    
}

.chat-room-last-seen{
    margin: 0;
}

.chat_header{
    display: flex;
    padding: 20px;
    align-items: center;
    border-bottom: 1px solid lightgray;
}

.chat_headerInfo{
    flex: 1 1;
    padding-left: 20px;
}

.chat_headerInfo > h3 {
    margin-bottom: 3px;
    font-size: 18px !important;
    font-weight: 400;
}

.chat_headerInfo > p {
    color:gray;
    font-size: 14px !important;
}

.chat_headerRight{
    display: flex;
    justify-content: space-between;
    min-width: 100px;
}

.chat_body{
    flex: 1 1;
    background-image: url('https://images.pexels.com/photos/707582/pexels-photo-707582.jpeg?auto=compress&cs=tinysrgb&w=1260&h=750&dpr=1');
    background-repeat: repeat;
    background-position: center;
    padding: 30px;
    overflow:scroll;
}

.chat_message{
    position: relative;
    font-size: 16px;
    border-radius: 10px;
    padding: 10px;
    background-color: white;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
}

.chat_name{
    position: absolute;
    top:-15px;
    font-weight: 800;
    font-size: xx-small;
}

.chat_timestemp{
    margin-left: 10px;
    font-size: xx-small;
}

.chat_receiver{
    margin-left: auto;
    background-color: #dcf8c6;
}

.chat_footer{
    display: flex;
    align-items: center;
    justify-content: space-between;
    height: 62px;
    border: 1px solid lightgray;
}

.chat_footer > form{
    flex: 1 1;
    display: flex;
}

.chat_footer > form > input{
    flex: 1 1;
    border-radius: 30px;
    padding: 10px;
    border: none;
    margin: 0 10px 0px 10px;
}

.chat_footer > form > button{
    display: none;
}

