#sidebar {
  flex-direction: end;
  z-index: 1000;
  position: fixed;
  margin-top: 0px;
  margin-bottom: 30em;
  left: 250px;
  width: 320px;
  height: 90%;
  margin-left: -250px;
  overflow-y: auto;
  background: #37474F;
  transition: all 0.5s ease;
}

#sidebar header {
  font-size: 20px;
  line-height: 52px;
  text-align: center;
}

#sidebar header a {
  font-size: 18px;
  height: 75px;
  color: #fff;
  display: block;
  background-color: rgba(0, 0, 0, 0.414);
  border-radius: 5px ;
  text-decoration: none;
}

#sidebar header a:hover {
  color: #fff;
}

#sidebar .nav li{
  padding:15px 0 0 80px;
  margin-bottom: 40px !important;
  margin-left: 0px !important;
}

#sidebar .nav li a{
  text-decoration: none;
  background: none;
  padding-bottom: 9px;
  border-bottom: 1px solid #455A64;
  color: #CFD8DC;
  font-size: 18px;
  justify-content: space-between;
 
}


#sidebar .nav a:hover{
  background: none;
  color: #ECEFF1;
}


